                                </div>
                            </template>
                        </b-overlay>
                    </b-col>
                </b-row>
                <b-row>
                    <b-col>
                        <div class="d-flex justify-content-center visualizer-text">
                            <div class="mr-2 mt-1">
                                <button @click="stopAudio" class="btn pt-2" style="background-color:var(--fColor);color:var(--bgColor);border-radius:10px;border: 2px solid var(--btnColor)"><b-icon :icon="playIcon"></b-icon></button>
                            </div>
                            <div>      
                                <p> Song Copyright 2019 : <a href="https://www.youtube.com/watch?v=pWAP7fIwGnI" _target="blank" class="hyperlink">"Dawn" by @Sappheiros </a> </p>
                            </div>
                        </div>
                    </b-col>
                </b-row>
            </b-col>
            <b-col class="card-headline p-1 p-sm-3 order-md-1 order-0" md="6" cols="12" lg="7" > <!-- introduction text-->
                <b-row>
                    <b-col ><h1 class="border-bottom-line text-center text-md-right">INTRODUCTION</h1></b-col>
                </b-row>
                <b-row>
                    <b-col class="introduction-body text-left px-sm-1 px-4  ">
                        <p>Hi! My Name is <strong>Keith Andrew Pacio</strong>. I'm most commonly called <strong>Kasper</strong> by my colleagues.</p>
                        <p>I'm a Computer Engineer that can work on both Software and Hardware Technologies.</p>
                        <p>I make Web Applications using PHP and JS technology and frameworks! I'm planning to expand my literacy in other languages. </p>
                        <p>I enjoy listening to Techno Music and enjoy playing Rhythm Games. I also love programming as a hobby! </p>
                        <p><strong>Feel free to know more about me by exploring my website!</strong></p>
                        <p>...or you can also<a class="hyperlink" href="/pdf/kasper_resume.pdf?v=2" download style="font-size:20px"> Download my Resume</a></p>
                    </b-col>
                </b-row>
            </b-col>
        </b-row>

        <b-row class="spacer-headline"></b-row> <!-- divider -->

