            <b-col>
                <b-row>
                    <b-col><h1>Technologies Used</h1></b-col>
                </b-row>
            </b-col>
        </b-row>

        <b-row>
            <b-col cols="12" lg="6" v-for="(skill) in skills" :key="skill.category">
                <b-row>
                    <b-col cols=12>
                        <b-row class="d-flex justify-content-center system-color pt-4">
                            <div class="px-sm-1 px-4">
                                <h2>{{skill.category}}</h2>
                            </div>
                        </b-row>
                        <b-row class="spacer-headline-thin"></b-row> <!-- divider -->
                    </b-col>
                    <b-col cols=12>
                        <b-row class="d-flex justify-content-center pt-3 flex-wrap">
                            <div v-for="(item) in skill.list" :key="item.name" class="px-4 py-3 d-flex flex-column justify-content-center" >
                                <div class="skill-icon bg-white d-flex align-items-center justify-content-center m-auto" style="border-radius:10%">
                                    <img :src=getPic(item.imgPath) style="width:90%;height:auto" :title=item.name />
                                </div>
                                <div class="d-flex flex-column pt-2">
                                    <h4 style="font-weight: bold;margin-bottom:0px!important" class="system-color">{{ item.name }}</h4>
                                    <p style="width:80%" class="m-auto">{{item.period}}</p>
                                </div>
                            </div>
                        </b-row>
                    </b-col> 
                </b-row>
            </b-col>
        </b-row>

        <b-row class="spacer-headline"></b-row> <!-- divider -->

        <b-row> <!-- text lead -->
            <b-col>
                <b-row>
                    <b-col><h1>PAGES</h1></b-col>
                </b-row>
            </b-col>
        </b-row>
        
        <!--pages -->
        <b-row class="card-margin p-sm-1 p-3" data-aos="fade-right"> <!-- card intro-->
            <b-col class="card-headline">
                <b-row>
                    <b-col cols="12" md="3">
                        <b-icon-person-circle class="icon-logo"></b-icon-person-circle>
                    </b-col>
                    <b-col cols="12" md="9">
                        <b-row>
                            <b-col>
                                <h1 class="border-bottom-line text-center text-md-left">Profile</h1>
                                <p>
                                    Get to know more about myself. Let me show you who I really am. 
                                </p>
                            </b-col>
                        </b-row>
                            
                        <b-row class="hyperlink">
                            <router-link to="/myself">Go to Page >></router-link>
                        </b-row>    
                    </b-col> 
                </b-row>
            </b-col>
        </b-row>

        <b-row class="spacer-headline-no-border"></b-row> <!-- divider -->

        <b-row class="card-margin p-sm-1 p-3" data-aos="fade-left"> <!-- card intro -->
            <b-col class="card-headline">
                <b-row>
                    <b-col cols="12" md="9" class="order-md-0 order-1">
                        <b-row>
                            <b-col>
                                <h1 class="border-bottom-line text-md-right text-center">Work Experiences</h1>
                                <p>
                                    Interested about my past jobs? Here! I'll show you the list of all my past and current ones.
                                </p>
                            </b-col>
                        </b-row>
                            
                        <b-row class="hyperlink">
                            <router-link to="/work">Go to Page >></router-link>
                        </b-row>    
                    </b-col>
                    <b-col cols="12" md="3" class="order-md-1 order-0">
                        <b-icon-person-badge class="icon-logo"></b-icon-person-badge>
                    </b-col> 
                </b-row>
            </b-col>
        </b-row>
 
        <b-row class="spacer-headline-no-border"></b-row>

        <b-row class="card-margin p-sm-1 p-3" data-aos="fade-right">
            <b-col class="card-headline">
                <b-row>
                    <b-col cols="12" md="3">
                        <b-icon-person-lines-fill class="icon-logo"></b-icon-person-lines-fill>
                    </b-col>
                    <b-col cols="12" md="9">
                        <b-row>
                            <b-col>
                                <h1 class="border-bottom-line text-md-left text-center">Personal Projects</h1>
                                <p>
                                   A list of projects I've created as a hobby.  Interested? Step right in!
                                </p>
                            </b-col>
                        </b-row>
                            
                        <b-row class="hyperlink">
                            <router-link to="/projects">Go to Page >></router-link>
                        </b-row>    
                    </b-col> 
                </b-row>
            </b-col>
        </b-row>

    </b-container>
</template>

<script>
