import Wave from "@foobar404/wave"
import ImageCard from '@/components/ImageCard.vue' 

export default {
  name: 'Index',
  components : {ImageCard},
  data(){
      return {
        show : true, //visualizer overlay
        playing : false,
        skills : [
            
        {
                category : "Languages and Scripts",
                list : [
                    {
                        name : "HTML",
                        imgPath : "assets/tech/html.svg",
                        period : "4 Years and 6 Months",
                    },
                    {
