                        imgPath : "assets/tech/smarty.gif",
                        period : "3 Years",
                    },
                    {
                        name : "Node.js",
                        imgPath : "assets/tech/nodejs.svg",
                        period : "1 Year",
                    },
                    {
                        name : "CodeIgniter",
                        imgPath : "assets/tech/codeigniter.svg",
                        period : "6 Months",
                    },
                    {
                        name : "Slim Framework",
                        imgPath : "assets/tech/slim.png",
                        period : "6 Months",
                    },
                    
                ]
            },
            {
                category : "Communication and Formats",
                list : [  
                    {
                        name : "JSON",
                        imgPath : "assets/tech/json.svg",
                        period : "2 Years",
                    },
                    {
                        name : "REST API",
                        imgPath : "assets/tech/rest_api.svg",
                        period : "2 Years",
                    },
                    {
                        name : "MQTT",
                        imgPath : "assets/tech/mqtt.svg",
                        period : "1 Year",
                    },
                    {
                        name : "Socket.IO",
                        imgPath : "assets/tech/socketio.svg",
                        period : "1 Year",
                    },
                    {
                        name : "Socketi",
                        imgPath : "assets/tech/soketi.png",
                        period : "1 Year",
                    },
                ]
            },
            {
                category : "Databases",
                list : [ 
                    {
                        name : "mySQL",
                        imgPath : "assets/tech/mysql.svg",
                        period : "4 Years and 6 Months",
                    }, 
                    {
                        name : "MongoDB",
                        imgPath : "assets/tech/mongodb.svg",
                        period : "1 Year",
                    }
                ]
            },
            {
                category : "Linux Servers",
                list : [ 
                    {
                        name : "Ubuntu",
                        imgPath : "assets/tech/ubuntu.svg",
                        period : "2 Years",
                    },
                    {
                        name : "AlmaLinux",
                        imgPath : "assets/tech/almalinux.svg",
