                    }, 
                    {
                        name : "CentOS",
                        imgPath : "assets/tech/centos.svg",
                        period : "1 Year",
                    },
                ]
            },
            {
                category : "Server Applications",
                list : [ 
                    {
                        name : "Apache",
                        imgPath : "assets/tech/apache.svg",
                        period : "2 Years",
                    },
                    {
                        name : "Nginx",
                        imgPath : "assets/tech/nginx.svg",
                        period : "2 Years",
                    }, 
                    {
                        name : "Grafana",
