                        imgPath : "assets/tech/redis.svg",
                        period : "6 Months",
                    },
                ]
            },
            {
                category : "Version Control",
                list : [ 
                    {
                        name : "GitHub",
                        imgPath : "assets/tech/github.svg",
                        period : "2 Years",
                    },
                    {
                        name : "GitLab",
                        imgPath : "assets/tech/gitlab.svg",
                        period : "2 Years",
                    }
                ]
            },
            {
                category : "Payment Gateways",
                list : [ 
                    {
                        name : "Stripe",
                        imgPath : "assets/tech/stripe.svg",
                        period : "1 Year",
                    },
                    {
                        name : "PayPal",
                        imgPath : "assets/tech/paypal.svg",
                        period : "1 Year",
                    },
                    {
                        name : "Mollie",
                        imgPath : "assets/tech/mollie.svg",
                        period : "2 Years",
                    }
                ]
            },
            {
                category : "Other Third Party APIs",
                list : [ 
                    {
                        name : "Google Maps",
                        imgPath : "assets/tech/google_maps.svg",
                        period : "3 Years",
                    },
                    {
                        name : "HubSpot",
                        imgPath : "assets/tech/hubspot.svg",
                        period : "1 Year",
                    },
                    {
                        name : "LINE",
                        imgPath : "assets/tech/line.svg",
                        period : "1 Year",
                    }
                ]
            },
            

            
        ]
      }
  },
  mounted(){
        this.createWave();
        //check if audio playing
        if(!document.getElementById('backgroundMusic').paused){
            this.show=false;
            this.playing=true;
        }
  },
  methods:{
       createWave(){
            let color1 = getComputedStyle(document.body).getPropertyValue('--fColor');
            let color2 = getComputedStyle(document.body).getPropertyValue('--btnColor');
            let wave = new Wave();
            let options = {
                type:"shockwave",
                stroke: 2,
                colors: [color2,color1,color2,color1]
            };
            wave.fromElement("backgroundMusic","visualizer", options);
      
            this.$refs.visualizer.click();
            
        },
       playAudio(){
            if(document.getElementById('backgroundMusic').paused){
                document.getElementById('backgroundMusic').play();
                document.getElementById('backgroundMusic').volume = 0.1;
                this.show=false;
                this.playing=true;
            }
        },
        stopAudio(){
            if(this.playing){
                document.getElementById('backgroundMusic').pause();
                document.getElementById('backgroundMusic').currentTime = 0;
                this.show=true;
                this.playing=false;
            }
            else{
                this.playAudio();
            }
        },   
        getPic (name) {
            return require("@/" + name);
        }
  },
  computed:{
       showVariant(){
           return this.$store.getters.showVariant;
        },
        playIcon(){
            return this.playing ? 'stop-circle-fill' : 'play-circle-fill';
        }
  },
  watch: {
      showVariant(){
        this.createWave(); //recreate wave base on theme mode
      }
  }
}
</script>

<style scoped>
.spacer-headline-thin{
    margin : 5% 0%;
    border: 1px solid var(--fColor);
    border-radius:10px;
}

.skill-icon {
    width:5rem;
    height:5rem;
}

.system-color{
    background: -webkit-linear-gradient(var(--fColor), var(--btnColor));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.introduction-body p{
    line-height: 1.1;
}

.introduction-body{
    padding-top: 15px;
}

.visualizer-holder{
    margin-top:2%;
    padding:0!important;
    border: 5px solid var(--fColor);
}

.visualizer-img{
    /* margin-top : 2%; */
    object-fit: fill;
    width: 100%;
    /* border-radius: 30px; */
    /* border:5px solid var(--fColor); */
}

.visualizer-text{
    margin-top:2%;
}

h1{
    background: -webkit-linear-gradient(var(--fColor), var(--btnColor));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.card-margin{
    margin: 7% 0;
}

.card-headline .hyperlink, .hyperlink{
    display: block;
    text-decoration: none;
    background: -webkit-linear-gradient(var(--fColor), var(--btnColor));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.spacer-headline{
    margin : 5% 0%;
    border-bottom: 2px solid var(--fColor);
}
.spacer-headline-no-border{
    margin : 5% 0%;
}

.card-headline{
    /* border: 2px solid var(--fColor); */
    /* border-radius:10px; */
    padding: 10px 10px;
    width: 70%;
}

.card-headline .card-headline-img{
    object-fit: cover;
    width: 70%;
    min-width: 150px;
    height: auto;
    border-radius: 20px;
    border:1px solid var(--fColor);
    
}

.border-bottom-line{
    border-bottom: 1px solid var(--fColor);
    text-align:left;
}

.text-headline .row div p{
    text-align:center;
    display: inline;
    font-size : 45px; 
    background: -webkit-linear-gradient(var(--fColor), var(--btnColor));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    animation: glow 2s ease-in-out infinite alternate;
}

.icon-logo{
    min-width: 120px; 
    height: auto;
    color: var(--fColor)!important;
}

@keyframes glow {
    from {
      text-shadow: 0 0 20px var(--btnColor);
    }
    to {
      text-shadow: 0 0 30px var(--btnColor), 0 0 10px var(--btnColor);
    }
}

</style>
